.left_icon{
    position: absolute;
    top:10px;
    left:10px;
}

.right_icon{
    position:absolute;
    bottom:10px;
    right:10px;
}

.season-display{
    display:flex;
    justify-content:center;
    align-items:center;
    height:100vh;
}

.summer{
    background-color:orange;
}

.winter{
    background-color:aliceblue;
}


.season-display.summer i {
    color:red;
}

.season-display.winter i {
    color:blue;
}